var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{attrs:{"id":"about-us"}},[_c('div',{staticClass:"about-us"},[_c('div',{staticClass:"about-text"},[_c('h3',[_vm._v("O nama")]),_c('p',[_vm._v("Nevladina organizacija PORTRET iz Bara sprovodi projekat KOFER PSIHO-ALATA podran kroz program Odgovorno i solidarno: graani preuzimaju inicijativu, koji realizuje Fond za aktivno graanstvo iz Podgorice - fAKT, u partnerstvu sa nevladinim organizacijama Udruenje roditelja djece sa tekoama u razvoju Zraak nade i Aktivna zona, uz finansijsku podrku Evropske unije i kofinansiranje Ministarstva javne uprave. ")]),_c('br'),(_vm.more===true)?_c('div',{staticClass:"more-about"},[_c('p',[_vm._v(" KOFER PSIHO - ALATA je program podrke za prelazak u odraslo doba, namijenjen mladima (15 - 29 godina) optine Bar, a zbog svojih digitalnih karakteristika bie dostupan svim mladim stanovnicima Crne Gore. Ima za cilj stvaranje psiholoke otpornosti u tranzitnom periodu adolescencije, razvijanje vjetina neophodnih za zdrav razvoj u savremenom, postcovid dobu i budjenje aktivizma u oblasti mentalnog zdravlja. Zamiljen je kao dvopla ormni program: Prva pla orma predstavlja ciklus od pet psiholokih radionica u okviru kojih e mladi imati priliku da se upoznaju sa linim strategijama brige o mentalnom zdravlju i budu osnaeni za zdrav dalji razvoj. Druga platforma podrazumijeva kreiranje digitalnog prostora na domenu koferpsihoalata.me i ima za cilj da okuplja mlade u virtuelnom prostoru koji treba da poslui kao alat podrke, edukacije, angaovanja i komunikacije na relevantne teme u oblasti mentalne higijene. Ovaj virtuelni prostor e nuditi relevantne informacije iz oblasti mentalnog zdravlja, kroz kontakt i komunikaciju e uticati na suzbijanje stigme u vezi sa temama iz ove oblasti , podsticati mlade da se angauju u vezi sa ovim temama ime e se na svojevrstan nain njegovati ideja mentalnog zdravlja u zajednici. Svaki alat pojedinano ima podciljeve. Podrka: virtuelni prostor ima za cilj da obezbijedi relevantne informacije u oblasti mentalnog zdravlja i higijene i da odgovori na brojna pitanja iz ove oblasti . Takoe, ima za cilj da predstavi relevantnu i provjerenu bazu podataka u vezi sa institucijama koje se bave mentalnim zdravljem, licenciranim psihoterapeutima, grupama podrke. Edukacija: virtuelni prostor ima za cilj da edukuje i odgovori na pitanja mladih iz oblasti mentalnog zdravlja i da prui informacije i da kreira abecedu mentalnog zdravlja. Kroz abecedu mentalnog zdravlja na struan i nauno relevantan nain bie odgovoreno na brojna pitanja kao to su npr. ta je mentalno zdravlje? Koja je razlika izmeu psihologa, psihoterapeuta i psihijatra? Kada je potrebno da se javim za strunu pomo ? ta je stigma? i slino. Angaovanje: virtuelni prostor ima za cilj da angauje mlade ljude da izraze i podijele stavove o mentalnom zdravlju i higijeni na kreativan nain. Ideja nam je da kroz eseje, pitanja, kreiranje video ili audio sadraja oslunemo ta je za mlade mentalno zdravlje, na koji nain razmiljaju o mentalnom zdravlju u zajednici, kako njeguju mentalno zdravlje, koje probleme vide u zajednici vezano za temu mentalnog zdravlja i higijene. Komunikacija: virtuelni prostor ima za cilj da pokrene komunikaciju izmeu mladih izmeu sebe ali i mladih i strune zajednice kroz formu foruma u kojem bi se diskutovalo o percepcijama problema sa kojima se mladi najee susreu u oblasti mentalnog zdravlja. Na taj nain ojaavala bi se mrea podrke i razumijevanje tema od znaaja iz oblasti mentalnog zdravlja, ne samo kroz saradnju sa strunim licima - nego iz kroz peer komunikaciju. ")]),_c('br'),_c('p',[_vm._v(" Ono to elimo da dostignemo je stvaranje adekvatnog sistema podrke za razvijanje psiholoke otpornosti kod mladih kako bi kao kompetentni pojedinci (samosvjesni, inova +tivni, otvoreni, tolerantni, osnaeni, kreativni, produktivni) prelazili u odraslo doba, samim tim bili zdravi i perspektivni lanovi zajednice. ")]),_c('br'),_vm._m(0),_c('p',[_vm._v(" Projekat traje od maja 2022. do juna 2023.godine. ")]),_c('br')]):_vm._e(),_c('button',{staticClass:"about-toggle",on:{"click":_vm.toggleMore}},[_vm._v("Prikai vie")])]),_vm._m(1)])])
}
